// tooltip
// tooltip
.egs-tooltip {
  margin: 0;
  cursor: pointer;
  &__modal-in-title {
    position: absolute;
    padding: 5px;
    opacity: 0;
    visibility: hidden;
    z-index: 40;
    width: 300px;
    height: 100px;
    width: max-content;
    max-width: 260px;
    height: max-content; 
    background: #ffffff;
    border: 1px solid #9d9d9d;
    border-radius: 10px;
    margin-top: 10px;
    top: 100%;
    left: 50%;
    transition: all .3s;
    transform: translate(-50%, 0);
    text-align: left;

  }
  &__modal-in-product {
    position: absolute;
    padding: 5px;
    opacity: 0;
    visibility: hidden;
    z-index: 40;
    width: max-content;
    max-width: 200px;
    background: #ffffff;
    border: 1px solid #9d9d9d;
    border-radius: 10px;
    margin-top: 10px;
    top: 100%;
    left: 50%;
    transition: all .3s;
    transform: translate(-50%, 0);
    text-align: left;
  }
  &__text {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $base-c;
  }
