    opacity: 1;
    visibility: visible;
  }
  .egs-tooltip__img:hover + .egs-tooltip__modal-in-title {
    font-size: 14px;
  }
  .egs-tooltip__img:hover + .egs-tooltip__modal-in-title {
    opacity: 1;
    visibility: visible;
  }

  
