<template>
  <div class="main__container container">
    <div v-show="openImgModal" class="modal" @click='closeImgModal'>
      <div class="modal__wrapper">
        <div class="modal__top">
          <div class="modal__counter">
            <span class="modal__num modal__num_start">{{indexElement}}</span>
            <span class="modal__num">/</span>
            <span class="modal__num modal__num_end">{{totalModalImg}}</span>
          </div>
          <button  @click='closeImgModal' type="button" class="modal__close"></button>
        </div>
        <div class="modal__arrows">
          <button @click="prevImg" type="button" class="modal__arrow modal__arrow_left"><img class="modal__arrow-img modal__arrow-img_left" src="../assets/img/arrow.svg" alt="Предыдущая"></button>
          <button @click="nextImg" type="button" class="modal__arrow modal__arrow_right"><img class="modal__arrow-img" src="../assets/img/arrow.svg" alt="Следующая"></button>
        </div>
        <div class="modal__content modal__content_stand">
          <div class="modal__img-box" :key="(item, i)"  v-for="(item, i) in this.info3.data">
            <img
              v-show="(i + 1) === indexElement"
              :id="i + 1"
              :src="require('../assets/img/pam/standartnie/' + item.url)"  
              alt="Фотография памятника" class="modal__image"
            >
          </div>
          
        </div>
      </div>  
    </div>
    <div class="main__content content-box">
      <div class="page-with-pagination__wrapper"> 
        <div class="page-with-pagination__category">
          <div class="page-with-pagination__title-box title-box">
            <h3 class="page-with-pagination__title title title_h3">Памятники из гранита - Стандартные</h3>
          </div>
          <div @click="imgsPreview" class="page-with-pagination__items page-with-pagination__items_stand">
            <div :key="(item, i)" v-for="(item, i) in this.inPage" class="page-with-pagination__item page-with-pagination__item_stand">
              <img :src="require('../assets/img/pam/standartnie/' + item.url)"  alt="Фотография памятника" class="page-with-pagination__item-img page-with-pagination__item-img_stand">
            </div>
          </div>
          <div @click="imgsPreview" class="page-with-pagination__items page-with-pagination__items_stand-vert">
            <div :key="(item, i)" v-for="(item, i) in this.inPage2" class="page-with-pagination__item page-with-pagination__item_stand-vert">
              <img :src="require('../assets/img/pam/standartnie/' + item.url)"  alt="Фотография памятника" class="page-with-pagination__item-img page-with-pagination__item-img_stand-vert">
            </div>
          </div>
        </div>
      </div>
      <egs-pagination :totalItem="this.info.length" :totalItemInPage="20" @changedPage="loadFiles"></egs-pagination>
    </div>
  </div>
</template>
<script>
  import Stand from '../assets/JSON/stand.json';
  import Stand2 from '../assets/JSON/stand2.json';
  import egsPagination from '../components/egs-pagination/egs-pagination.vue'

  export default {
    data() {
      return {
        info3: {
          "data": []
        },
        info2: Stand2.data,
        info: Stand.data,
        totalItemInPage: 20,
        inPage2: null,
        inPage: null,
        prewiewData: Stand,
        indexElement: 0,
        context: this.contextIn,
        openImgModal: false,
        totalModalImg: 0,
      }
    },
    components: {
      'egs-pagination': egsPagination,
    },
    mounted() {
    },
    created() {
      this.loadFiles();
      this.loadFiles2();
    },
    methods: {
      loadFiles(pageNumber) {
        if(pageNumber) {
          let start = this.totalItemInPage * pageNumber - 20;
          let end = this.totalItemInPage*pageNumber;
          this.inPage = this.info.slice(start, end)
        } else {
          if(this.info)
            this.inPage = this.info.slice(0, this.totalItemInPage)
        }
      },
      loadFiles2(pageNumber) {
        if(pageNumber) {
          let start = this.totalItemInPage * pageNumber - 20;
          let end = this.totalItemInPage*pageNumber;
          this.inPage2 = this.info2.slice(start, end)
        } else {
          if(this.info2)
            this.inPage2 = this.info2.slice(0, this.totalItemInPage)
        }
      },
      imgsPreview(event) {
        if(event.target.classList.contains('page-with-pagination__item-img')) {
          const body = document.querySelector('body');
          const element = event.target;
          
          let elementSrc = element.getAttribute('src').split("/").reverse().shift();
          let elementSrcNumber = elementSrc.split(".").shift();
          this.info3.data = this.info.concat(this.info2);
          this.totalModalImg = this.info.length + this.info2.length;
          this.openImgModal = true;
          this.imgSrc = elementSrc;
          this.indexElement = elementSrcNumber;
          this.indexElement = parseInt(this.indexElement) + 1 - 1;
          body.style.overflow = 'hidden';
          
        }
      },
      prevImg() {
        if(this.indexElement > 1) {
          this.indexElement--;
        }
      },
      nextImg() {
        if(this.indexElement < this.totalModalImg) {
          this.indexElement++;
        }
      },
      closeImgModal(event) {
        if(event.target.classList.contains('modal') || event.target.classList.contains('modal__close')) {
          const body = document.querySelector('body');
          this.openImgModal = false;
          body.style.overflow = 'visible';
        }
          
      }
    },
    computed: {}
  }
</script>
<style lang="scss">
.page-with-pagination {
  &__wrapper {
    padding: 30px 25px 50px 25px;
  }
  &__category {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  &__title-box {
    max-width: 650px !important;
    margin: 0 0 25px 0;
  }
  &__items {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    &_portret {
      justify-content: center;
    }
    &_stand {
      // justify-content: center;
    }
    &_stand-vert {
      justify-content: space-between;
      margin-top: 30px;
    }
  }
  &__item {
    cursor: pointer;
    display: flex;
    flex-direction: column; 
    max-width: 230px;
    max-height: 350px;
    border-radius: 10px !important;
    overflow: hidden;
    background: #c3c3c3;
    &_peiz {
      max-width: max-content !important;
    }
    &_portret {
      max-width: max-content !important;
    }
    &_stand {
      max-height: max-content !important;
    }
    &_stand-vert {
      width: 100%;
      max-width: max-content !important;
    }
  }
  &__item-img {
    min-width: 230px;
    min-height: 270px;
    &_peiz {
      width: 230px;
      height: 100% !important;
    }
    &_portret {
      width: 100% !important;
    }
    &_stand {
      height: 430px !important;
    }
    &_stand-vert {
      flex: 1 1 100%;
      width: 600px !important;
    }
  }
  &__item-button {
    width: 100%;
    font-size: 14px;
    // font-weight: 400 !important;
    padding: 10px 10px;
    border-radius: 0 !important;
  }
}
.modal__content_stand {
  max-width: 100%;
}
@import '../components/egs-pagination/egs-pagination';
</style>