<template>
  <nav class="nav">
    <div class="nav__container container">
      <button @click="openBurger" class="burger" type="button"><span></span></button>
      <ul class="nav__list">
        <button @click="closeBurger" class="burger-close burger" type="button"></button>
        <li @click="closeBurger" class="nav__item"><router-link :to="{name: 'home'}" class="nav__link"><span>Главная</span></router-link></li>
        <li @click="closeBurger" class="nav__item"><router-link :to="{name: 'pam'}" class="nav__link"><span>Памятники</span></router-link>
        </li>
        <li @click="closeBurger" class="nav__item"><router-link :to="{name: 'imp'}" class="nav__link"><span>Благоустройство</span></router-link></li>
        <li @click="closeBurger" class="nav__item"><router-link :to="{name: 'psp'}" class="nav__link"><span>Плитки на кресты</span></router-link></li>
        <li @click="closeBurger" class="nav__item"><router-link :to="{name: 'acces'}" class="nav__link"><span>Аксессуары</span></router-link></li>
        <li @click="closeBurger" class="nav__item"> <router-link :to="{name: 'our-work'}" class="nav__link" href="/our-work"> <span>Наши работы</span></router-link></li>
        <li @click="closeBurger" class="nav__item"> <router-link :to="{name: 'contacts'}" class="nav__link" href="/contacts"> <span>Контакты</span></router-link></li>
      </ul>
    </div>
  </nav>
</template>
<script>
  export default {
    data() {
      return {
        isOpenBurger: false
      }
    },
    methods: {
      openBurger() {
        this.isOpenBurger = true;
        const navList = document.querySelector('.nav__list');
        const body = document.querySelector('body');

        body.style.overflow = 'hidden';
        navList.classList.add('_isOpen');
      },
      closeBurger() {
        this.isOpenBurger = false;
        const navList = document.querySelector('.nav__list');
        const body = document.querySelector('body');

        body.style.overflow = 'visible';
        navList.classList.remove('_isOpen');
      }
    }
  }
</script>

<style lang='scss'>
  @import './dropdown-menu';

.nav {
  min-height: 60px;
  height: 60px;
  background: #6C9AC8;
  width: 100%;
  &__list {
    width: 100%;
    display: flex;
    align-items: center;
  }
  &__item {
    display: flex;
    align-items: center;
    position: relative;
    flex: 1 0 auto;
    background: #6C9AC8;
    max-height: 60px;
  }
  &__link {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1 1 auto;
    gap: 10px;
    padding: 20px;
    transition: all .3s;
  }
  &__link:hover {
    background: #4F769C;
  }
  &__link span{
    font-weight: 400 !important;
    font-size: 20px;
  }
}
.burger {
  display: none;
  visibility: hidden;
}
@media screen and (max-width: 868px) {
  .nav {
    &__link span{
      font-weight: 400;
      font-size: 18px;
    }
  }
}
@media screen and (max-width: 568px) {
  .nav {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    &__container {
      width: 100%;
      padding: 0 25px 0 25px;
      display: -webkit-flex;
      display: -ms-flex;
      display: flex;
      justify-content: flex-end;
    }
    &__list {
      z-index: 1000;
      display: none;
      position: fixed;
      top: 60%;
      left: 50%;
      transform: translate(-50%, -50%);
      flex-direction: column;
      visibility: hidden;
      background: #6C9AC8 !important;
      border: 0 !important;
      width: 100%;
      height: 100vh;
    }
    &__item {
      z-index: 1000;
    }
    &__list::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 100vh;
      background: #6C9AC8;
      top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: -100;
    }
    &__list._isOpen {
      display: -webkit-flex;
      display: -ms-flex;
      display: flex !important;
      visibility: visible !important;
    }
    &__list:first-child {
      margin-top: 170px;
    }
  }


}

@media screen and (max-width: 568px) {
  .burger {
    z-index: 300;
    width: 30px;
    height: 26px;
    background-color: transparent;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    visibility: visible;
    position: relative;
    &::after,
    &::before,
    &>span {
      content: '';
      position: absolute;
      width: 30px;
      height: 2px;
      background: #fff;
      top: 0;
      left: 0;
    }
    &::after {
      top: 50%;
    }
    &::before {
      top: 100%;
    }
    &-close {
      top: 0;
      right: 0;
      margin: 0px 40px 0 0;
      position: absolute;
      &::after {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(45deg)
      }
      &::before {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-45deg)
      }
    }
  }

}

</style>