<template>
  <header class="header">
    <div class="header__container container"> 
      <div class="header__wrapper"><a class="header__logo logo" href="/"> <span>Эконом Гранит</span></a>
        <div class="header__contacts"> <a class="header__contact" href="tel:">Тел: +7 (903) 347-55-86</a><a class="header__contact" href="tel:">Тел: +7 (903) 347-55-86</a><a class="header__contact" href="mailto:info.granit@mail.ru">E-mail: info.granit@mail.ru</a></div>
        <div class="header__buttons"> 
          <button @click="showForm" class="header__button button" type="button"> <img class="header__icon" src="../../assets/img/icons/tel.svg" alt="tel"><span class="header__text">заказать звонок</span></button>
          <a target="_blank" href="https://wa.me/79093206481?text=Здравствуйте,%20я%20хотел%20бы%20задать%20вопрос." class="header__button button" type="button"> <img class="header__icon" src="../../assets/img/icons/quest.svg" alt="tel"><span class="header__text">задать вопрос</span></a>
        </div>
      </div>
      <form class="modal-form " :class="{open: isOpen}">
        <div class="modal-form__wrapper">
          <div class="modal-form__title-box">
            <div class="modal-form__title title title_h4">Заказать звонок</div>
            <button class="modal-form__close" @click="notShowForm"></button>
          </div>
          <div  class="modal-form__body">
            <div class="modal-form__inputs">
              <input name="fullname" type="text" class="modal-apply__input  _req" placeholder="ФИО">
              <input name="number" type="text" pattern="^(\+7|7|8) ?\(?[0-9]{3}\)?[-| ]?[0-9]{3}[-| ]?[0-9]{2}[-| ]?[0-9]{2}$" class="modal-apply__input _number _req" placeholder="Номер телефона">
            </div>
            <button @click="sendingForm" type="submit" class="modal-form__button button">отправить</button>
          </div>
        </div>
      </form>
    </div>
  </header>
</template>

<script>
  export default {
    data() {
      return {
        isOpen: false
      }
    },
    methods: {
      showForm() {
        this.isOpen = true;
      },
      notShowForm() {
        this.isOpen = false;
      },
      sendingForm() {
        const sendingForm = document.querySelector('.modal-form');
        // const thanksPopup = document.querySelector('.thanks-popup');
        const validateRegNumber = /^(\+7|7|8) ?\(?[0-9]{3}\)?[-| ]?[0-9]{3}[-| ]?[0-9]{2}[-| ]?[0-9]{2}$/;
        async function formSend() {
          sendingForm.addEventListener('submit', (e) => {
            e.preventDefault();
          })
          let error = formValidate(sendingForm);  
          let formData = new FormData(sendingForm);
          

          if(error === 0) {
            sendingForm.classList.add('_sending');
            let response = await fetch('sendtelegram.php', {
              method: 'POST',
              body: formData,
            });
            if(response.ok) {
              // let result = await response.json();
              sendingForm.reset();
              // thanksPopup.classList.add('active');
              // const thanksPopupClose = document.querySelector('.thanks-popup__close');
              // thanksPopupClose.addEventListener('click', () => {
              //   thanksPopup.classList.remove('active');
              // });
              sendingForm.classList.remove('_sending');
            } else {
              console.log('hi')
              // const thanksPopupClose = document.querySelector('.thanks-popup__close');
              // thanksPopupClose.addEventListener('click', () => {
              //   thanksPopup.classList.remove('active');
              // });
              sendingForm.classList.remove('_sending');
              
            }

          } else {
            // alert('Заполните обязательные поля');
          }
        }

        const formValidate = (form) => {
          let error = 0;
          const sendingFormReq = form.querySelectorAll('._req');

          sendingFormReq.forEach((item) => {
            const input = item;

            formRemoveError(input);

            if(input.closest('._number')) {
              const numberCheck = validateRegNumber.test(input.value);
              if (!numberCheck) {
                formAddError(input);
                error++;
              }
            } else if((input.value === '') || (input.value.length > 40)) {
              formAddError(input);
              error++;
            }
          });
          return error;
        }


        const formAddError = (input) => {
          input.classList.add('_error');
        }

        const formRemoveError = (input) => {
          input.classList.remove('_error');
        }
        formSend();
      }
    }
  }

</script>

<style lang='scss'>
.modal-form {
  display: none;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, .4);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all .3s;
  z-index: 1000;
  &.open {
    display: block;
    opacity: 1;
    visibility: visible;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    position: absolute;
    gap: 20px;
    margin: 0 auto;
    background: #fff;
    width: 400px;
    padding: 25px;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
  }
  &__title-box {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
    color: #000;  
  }
  &__close {
    color: #000;
    position: relative;
    width: 30px;
    height: 30px;
    background: none;
  }
  &__close::after,
  &__close::before {
    content: '';
    position: absolute;
    width: 20px;
    height: 2px;
    background: #000;
    top: 50%;
    right: 0;

  }
  &__close::after {
    transform: translate(0, -50%) rotate(45deg);
  }
  &__close::before {
    transform: translate(0, -50%) rotate(-45deg);
  }
  &__title {
    width: 100%;
    text-align: center;
  }
  &__body {

  }
  &__inputs {
    display: flex;
    flex-direction: column;
    gap: 15px;
    margin: 0 0 15px 0;
  }
  &__input {
    border: 1px solid #000;
    border-radius: 5px;
    color: #000;
    padding: 10px 15px;
  }
  &__button {
    margin: 0 auto;
    padding: 5px 25px;
  }
}
.header {
  &__container {
    width: 100%;
    display: flex;
    padding: 25px 25px 25px 25px;
  }
  &__wrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo>span {
    color: $base-c;
  }
  &__contacts {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 5px;
  }
  &__contact {
    color: $base-c;
    font-weight: 500;
  }
  &__buttons {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  &__button {
    gap: 10px;
    padding: 10px 35px;
  }
  &__icon {
    width: 15px;
    height: 15px;
  }
  &__button span {
    font-size: 14px;
    font-weight: 300;
  }
}
@media screen and (max-width: 1024px) {
  .header {
    &__button span {
      font-size: 12px;
      font-weight: 300;
      margin-top: 3px;
    }
  }
}
@media screen and (max-width: 768px) {
  .header {
    &__logo>span {
      font-size: 20px;
    }
    &__contact {
      color: $base-c;
      font-size: 14px;
      font-weight: 500;
    }
    &__button {
      padding: 10px 10px;
    }
    // &__button span {
    //   display: none;
    //   visibility: hidden;
    // }
  }
}
@media screen and (max-width: 568px) {
  .header {
    &__container {
      justify-content: center;
    }
    &__wrapper {
      width: max-content;
      flex-direction: column;
      gap: 10px;
    }
    &__logo>span {
      font-size: 20px;
    }
    &__contact {
      color: $base-c;
      font-size: 14px;
      font-weight: 500;
    }
    &__buttons {
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      gap: 20px;
    }
    &__button {
      padding: 15px 30px;
    }
    &__button span {
      display: none;
      visibility: hidden;
    }
  }
}
// @import '../nav/nav';

</style>
