ной стороне памятника, являются пейзаж. \n                          Как правило, основным содержанием пейзажа на стеле является\n                           лесная местность. Иногда на таких изображениях присутствуют \n                           реки, озёра, поля, дикие животные и другие объекты живой и \n                           неживой природы. Большой популярностью в России пользуются \n                           изображения берёз — их гравируют и на лицевой, и на тыльной сторонах стелы.","src":"category/2/obrs.jpg","href":"peizaji"}}),_c('egs-catalog-item',{attrs:{"title":"Обратная сторона","tooltipText":"Обратная сторона памятника из гранита \n                          или другого материала часто оставляется однотонной. \n                          Если же она хорошо видна и к ней имеется доступ, то лучше уделить внимание и ее оформлению. ","src":"category/2/obrs.jpg","href":"obrs"}}),_c('egs-catalog-item',{attrs:{"title":"Портреты","tooltipText":"Портрет на памятник чаще всего наносят лазерной гравировкой. \n                          Наша мастерская выполняет портреты на граните и мраморе. На гранитные и мраморные надгробия осуществляется как лазерная гравировка фотографий, так и ручная. \n                          При помощи специального граверного оборудования и других инструментов, портрет на камне вырезается профессиональным художником.","src":"category/2/portreti.jpg","href":"portreti"}}),_c('egs-catalog-item',{attrs:{"title":"Свечи","tooltipText":"Наши специалисты могут нанести разнообразные рисунки свечей на памятниках. \n                          Так, одним из самых популярных является одна горящая или потухшая свеча. \n                          Такое простое и легкое изображение может украшать как женское, так и мужское или детское надгробие. \n                          Кроме того, оно будет гармонично сочетаться с любыми другими элементами оформления.","src":"category/2/svechi.jpg","href":"svechi"}}),_c('egs-catalog-item',{attrs:{"title":"Цветы","too