<template>
      <div class="main__pagination pagination">
        <div class="pagination__wrapper"> 
          <div class="pagination__pages"> 
            <button :key="page" v-for="page in totalPages"
              class="pagination__button pagination__button_page"
              :class="{_active: currentPage === page}"  
              @click="changePage(page)"
              :id="'page_' + page" type="button">
              <span class="pagination__number">{{page}}</span>
            </button>
          </div>
        </div>
      </div>
  
</template>
<script>
  export default {
    props: ['totalItem', 'totalItemInPage'],
    data() {
      return {
        currentPage: 1
      }
    },
    computed: {
      totalPages() {
        return Math.ceil(this.totalItem / this.totalItemInPage);
      }
    },
    methods: {
      changePage(pageNumber) {
         window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
        this.currentPage = pageNumber;

        this.$emit('changedPage', pageNumber);
      }
    }
  }
</script>