<template>
  <div class="main__container container">
    <div class="main__content content-box">
      <div class="page-with-pagination__wrapper"> 
        <div class="page-with-pagination__category">
          <div class="page-with-pagination__title-box title-box">
            <h3 class="page-with-pagination__title title title_h3">Гравировка - свечи</h3>
            <div class="egs-catalog__tooltip egs-tooltip">
              <img src="../assets/img/tooltip.svg" alt="Картинка тултайпа" class="egs-tooltip__big egs-tooltip__img">
              <div class="egs-tooltip__modal-in-title">
                <span class="egs-tooltip__text">Наши специалисты могут нанести разнообразные рисунки свечей на памятниках. 
                  Так, одним из самых популярных является одна горящая или потухшая свеча. 
                  Такое простое и легкое изображение может украшать как женское, так и мужское или детское надгробие. 
                  Кроме того, оно будет гармонично сочетаться с любыми другими элементами оформления.
                </span>
              </div>
            </div>
          </div>
          <div class="page-with-pagination__items">
            <div :key="(item, i)" v-for="(item, i) in this.inPage" class="page-with-pagination__item">
              <img :src="require('../assets/img/graw/svechi/' + item.url)"  alt="Фотография памятника" class="page-with-pagination__item-img">
              <!-- <button class="page-with-pagination__link" type="button">
                <img src="" alt="" class="page-with-pagination__item-img">
                :src="info[i].url"
              </button> -->
            </div>
          </div>
        </div>
      </div>
      <egs-pagination :totalItem="this.info.length" :totalItemInPage="20" @changedPage="loadFiles"></egs-pagination>
      
    </div>
  </div>
</template>
<script>
  import Svech from '../assets/JSON/svech.json';

  // import axios from 'axios'
  import egsPagination from '../components/egs-pagination/egs-pagination.vue'

  export default {
    data() {
      return {
        info: Svech.data,
        totalItemInPage: 20,
        inPage: null
      }
    },
    components: {
      'egs-pagination': egsPagination
    },
    mounted() {
    },
    created() {
      this.loadFiles();
    },
    methods: {
      loadFiles(pageNumber) {
        if(pageNumber) {
          let start = this.totalItemInPage * pageNumber - 20;
          let end = this.totalItemInPage*pageNumber;
          this.inPage = this.info.slice(start, end)
        } else {
          if(this.info)
            this.inPage = this.info.slice(0, this.totalItemInPage)
        }
      }
    },
    computed: {}
  }
</script>