      </div>
      
    </div>
  </div>
</template>
<script>
  import Ow from '../assets/JSON/ow.json';
  import egsImgPreview from '../components/egs-img-preview/egs-img-preview'
  export default {
    data() {
      return {
        imgSrc: '',
        openImgModal: false,
        elementNumber: 0,
        context: '',
        prewiewData: Ow
      }
    },
    components: {
      'egs-img-preview': egsImgPreview
    },
    methods: {
      imgsPreview(event) {
        if(event.target.classList.contains('our-work__img')) {
          const body = document.querySelector('body');
          const element = event.target;

          let elementSrc = element.getAttribute('src').split("/").reverse().shift();
          let elementSrcNumber = elementSrc.split(".").shift();
          this.openImgModal = true;
          this.imgSrc = elementSrc;
          this.elementNumber = elementSrcNumber;
          body.style.overflow = 'hidden';
          
          console.log(this.elementNumber);
          
        }
      },
      closeImgModal(event) {
        if(event.target.classList.contains('modal') || event.target.classList.contains('modal__close')) {
          const body = document.querySelector('body');
          this.openImgModal = false;
          body.style.overflow = 'visible';
        }
          
      }
    }
    
  }
</script>
<style lang="scss">
