  <div class="main__container container">
    
    <div class="main__page-with-examples page-with-examples"> 
      <egs-img-preview @closeImgModal="closeImgModal"  v-if='openImgModal' :imgSrcP='imgSrc' ></egs-img-preview>
      <div class="page-with-examples__wrapper">
        <div class="page-with-examples__title-box title-box">
        <h3 class="title title_h3">Мраморная крошка</h3>
        <div class="egs-catalog__tooltip egs-tooltip">
          <img src="../assets/img/tooltip.svg" alt="Картинка тултайпа" class="egs-tooltip__big egs-tooltip__img">
          <div class="egs-tooltip__modal-in-title">
            <span class="egs-tooltip__text">Мраморная крошка</span>
          </div>
        </div>
        </div>
        <div class="page-with-examples__products"> 
          <div class="page-with-examples__product"> 
            <div class="page-with-examples__product-wrapper"> 
              <div class="page-with-examples__product-img-box">
                <img src="../assets/img/blago/mramor/mr.jpg" alt="Фотография мраморной крошки" class="page-with-examples__product-img">
                <button class="page-with-examples__product-button button">Оставить заявку</button>
              </div>
              <h5 class="page-with-examples__product-title title title_h5">Мраморная крошка</h5>
            </div>
          </div>
        </div>
        <div class="page-with-examples__example example">
          <div class="example__wrapper">
            <div class="example__list">
              <div class="example__item">
                <h4 class="example__item-title title title_h4">Марморная крошка</h4>
                <div class="example__item-imgs"
                     @click="imgsPreview"
                >
                  
                  <div class="example__item-img-box">
                    <img src="../assets/img/blago/mramor/primer-mr-1.jpeg" alt="Фотография с примерами работ с использованием мраморной крошки" class="example__item-img">
                  </div>
                  <!-- ===1=== -->
                  <div class="example__item-img-box">
                    <img src="../assets/img/blago/mramor/primer-mr-2.jpeg" alt="Фотография с примерами работ с использованием мраморной крошки" class="example__item-img">
                  </div>
                  <!-- ===2=== -->
                  <div class="example__item-img-box">
                    <img src="../assets/img/blago/mramor/primer-mr-3.jpeg" alt="Фотография с примерами работ с использованием мраморной крошки" class="example__item-img">
                  </div>
                  <!-- ===3=== -->
                  <div class="example__item-img-box">
                    <img src="../assets/img/blago/mramor/primer-mr-4.jpeg" alt="Фотография с примерами работ с использованием мраморной крошки" class="example__item-img">
                  </div>
                  <!-- ===4=== -->
                  <div class="example__item-img-box">
                    <img src="../assets/img/blago/mramor/primer-mr-5.jpeg" alt="Фотография с примерами работ с использованием мраморной крошки" class="example__item-img">
                  </div>
                  <!-- ===5=== -->
                  <div class="example__item-img-box">
                    <img src="../assets/img/blago/mramor/primer-mr-6.jpeg" alt="Фотография с примерами работ с использованием мраморной крошки" class="example__item-img">
                  </div>
                  <!-- ===6=== -->
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
    </div>
  </div>
</template>
<script>
  import egsImgPreview from '../components/egs-img-preview/egs-img-preview'

  export default {
    data() {
      return {
        imgSrc: '',
        openImgModal: false
      }
    },
    components: {
      'egs-img-preview': egsImgPreview
    },
    methods: {
      imgsPreview(event) {
        if(event.target.classList.contains('example__item-img')) {
          const body = document.querySelector('body');
          const element = event.target;

          let elementSrc = element.getAttribute('src');
          
          this.openImgModal = true;
          this.imgSrc = elementSrc;
          body.style.overflow = 'hidden';
        }
      },
      closeImgModal() {
        const body = document.querySelector('body');
        this.openImgModal = false;
        body.style.overflow = 'visible';
      }
    }
    
  }
  
</script>
