<template>
  <div v-show="open" class="modal" @click='closeImgModal'>
    
    <div class="modal__wrapper">
      <div class="modal__top">
        <div class="modal__counter">
          <span class="modal__num modal__num_start">{{indexElement}}</span>
          <span class="modal__num">/</span>
          <span class="modal__num modal__num_end">{{totalItemInPage}}</span>
        </div>
        <button  @click='closeImgModal' type="button" class="modal__close"></button>
      </div>
      <div class="modal__arrows">
        <button @click="prevImg" type="button" class="modal__arrow modal__arrow_left"><img class="modal__arrow-img modal__arrow-img_left" src="../../assets/img/arrow.svg" alt="Предыдущая"></button>
        <button @click="nextImg" type="button" class="modal__arrow modal__arrow_right"><img class="modal__arrow-img" src="../../assets/img/arrow.svg" alt="Следующая"></button>
      </div>
      <div class="modal__content">
        <div class="modal__img-box" :key="(item, i)"  v-for="(item, i) in this.info">
          <img
            v-show="(i + 1) === indexElement"
            :id="i + 1"
            :src="require('../../assets/img/our-work/' + item.url)"  
            alt="Фотография памятника" class="modal__image"
          >
        </div>
        
      </div>
    </div>  
    
  </div>    
</template>
<script>
  export default {
    props: ['imgSrcP', 'idImg', 'open', 'data', 'contextIn'],
    data() {
      return {
        info: this.data.data,
        indexElement: this.idImg,
        context: this.contextIn,
        numberImg: 0
      }
    },
    created() {
      this.indexElement = parseInt(this.indexElement) + 1 - 1;
      this.context = '' + this.context
      console.log(this.context)

      this.loadImgs();
    },
    methods: {
      prevImg() {
        if(this.indexElement > 1) {
          this.indexElement--;
        }
      },
      nextImg() {
        if(this.indexElement < this.totalItemInPage) {
          this.indexElement++;
        }
      },
      loadImgs() {
        this.totalItemInPage = this.info.length;
      },
      closeImgModal() {
        this.$emit('closeImgModal', event)
      }
    }
  }
</script>
<style lang="scss">
/* Стили модального окна */
.modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  top: 50%;
  left: 50%;
  z-index: 1050;
  transform: translate(-50%, -50%);
  &::before {
    content: '';
    background: rgba(0, 0, 0, 0.7);
    width: 100vw;
    height: 100vh;
    position: absolute;
  }
  &__wrapper {
    z-index: 1052;
    max-width: 800px;
    max-height: 95vh;
    overflow: hidden;
  }
  &__top {
    z-index: 1053;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  &__close {
    z-index: 1060;
    width: 40px;
    height: 40px;
    background: none;
    position: relative;
  }
  &__close::before,
  &__close::after {
    content: '';
    background: #fff;
    top: 50%;
    left: 50%;
    width: 25px;
    height: 2px;
    position: absolute;
    border-radius: 10px;
  }
  &__close::after {
    transform: translate(-50%, -50%) rotate(45deg);
  }
  &__close::before {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  &__content {
    position: relative;
    display: flex;
    width: max-content;
    max-height: 800px;
    overflow: hidden;
    margin: 10px 0px 0 0px;
  }
  &__arrows {
    width: 80vw;
    z-index: 1055;
    height: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__arrow {
    display: flex;
    align-items: center;
    text-align: center;
    height: 100%;
    background: none;
    &-img {
      width: 40px;
      height: 40px;
      color: #fff000;
      transform: rotate(-90deg);
      fill: #fff;
      &_left {
        transform: rotate(90deg);
      }
    }
  }
  &__arrow_right {
    justify-content: flex-end;
  }
  &__img-box {
    display: flex;
  }
  &__image {
    width: 100%;
    height: 800px;
  }
}



